* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 42px;

    .menu_link {
      text-decoration: none;
      font-weight: 300;
      color: #FFFFFF;
      font-size: 15px; } } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    font-weight: 100;
    font-size: 13px;
    color: #FFFFFF; } }

.subheader_call {
  text-align: right;
  margin-top: 19px;
  font-weight: 300;
  font-size: 12px;
  color: #FFFFFF; }

.subheader_phone {
  display: block;
  margin-top: 8px;
  font-weight: 400;
  font-size: 22px;
  color: #FFFFFF;
  text-decoration: none;
  text-align: right;
  line-height: 12px; }

.subheader .subheader_btn {
  display: block;
  margin-top: 19px;
  width: 172px;
  height: 46px;
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
  background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
  border: none;
  border-radius: 4px;
  font-weight: 400;
  font-size: 14px;
  text-transform: uppercase;
  color: #FFFFFF; }

.promo {
  min-height: 800px;
  background-image: url(../img/main_bg.png);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    font-weight: 700;
    font-size: 60px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 60px;
    margin-bottom: 0px; }

  .promo_subheader {
    font-weight: 700;
    font-size: 36px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 36px;
    margin-bottom: 0px;
    margin-top: 22px; }

  .promo_descr {
    margin-top: 23px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    border: none;
    border-radius: 4px;
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    color: #FFFFFF; } }

.reasons {
  padding: 18px 0 65px 0; }

.label {
  display: block;
  margin: 0 auto;
  width: 113px;
  height: 24px;
  background-color: black;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
  line-height: 24px;
  border-radius: 3px; }

.title {
  margin-top: 33px;
  text-align: center;
  font-weight: 700;
  font-size: 38px;
  text-transform: uppercase;
  color: #222222; }

.subtitle {
    font-weight: 400;
    font-size: 17px;
    text-align: center;
    color: #222222;
    margin-top: 1px; }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    width: 116px;
    height: 116px;
    background-color: #1EACC7;
    border-radius: 100%;
    align-items: center;
    justify-content: center;
    margin-right: 25px; } }

.pros_text {
  width: 343px;
  margin-top: 0px;
  font-weight: 300;
  font-size: 15px;
  line-height: 21px;
  color: #212121;

  span {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #1EACC7; } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background-image: url(../img/cross.png);
    background-position: center;
    background-repeat: no-repeat;
    background-clip: cover;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 88px;
        &_subtitle {
            font-weight: 400;
            font-size: 24px;
            line-height: 31px;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: center; }
        &_1 {
            background-image: url("../img/mob_1.png");
            background-position: center;
            background-repeat: no-repeat;
            background-clip: cover; }
        &_2 {
            background-image: url("../img/mob_2.png");
            background-position: center;
            background-repeat: no-repeat;
            background-clip: cover; }
        &_3 {
            background-image: url("../img/mob_3.png");
            background-position: center;
            background-repeat: no-repeat;
            background-clip: cover; }
        &_4 {
            background-image: url("../img/mob_4.png");
            background-position: center;
            background-repeat: no-repeat;
            background-clip: cover; }
        &_5 {
            background-image: url("../img/mob_5.png");
            background-position: center;
            background-repeat: no-repeat;
            background-clip: cover; }
        &_6 {
            background-image: url("../img/mob_6.png");
            background-position: center;
            background-repeat: no-repeat;
            background-clip: cover; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 29px;
            height: 29px;
            background: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer; } } }





